import React from "react";
//import components materialse 
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import Edit from "@material-ui/icons/Edit";
import VisibilityIcon from '@material-ui/icons/Visibility';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import ModalAddPronostic from './ModalAddPronostic';
import { getListPronosticsVIP, getListPronosticsComboTips, getListPronosticsTodayTips, getListOldTipsTwoDayAgo, getListOldTipsYesterday, getListOldTipsThreeDayAgo, addPronostic, deletePronostic, updatePronostic } from '../../Services/Pronostics/PronosticsService';
import Switch from "react-switch";

// reactstrap components
import {
    Card, CardHeader, Table, CardTitle, CardBody, Col, Input
} from "reactstrap";

class VipTipsTable extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            setOpenModal: false,
            pronosticListTodayTips: [],
            pronosticListComboTips: [],
            pronosticListVip: [],
            oldTipsThreeDayAgo: [],
            oldTipsTwoDayAgo: [],
            oldTipsYesterday: [],
            currentDateOldTips: '1 Day Ago',
            checked: false
        };
        this.handleChangeCheckbox = this.handleChangeCheckbox.bind(this);
        this.onChangeDateOldTips = this.onChangeDateOldTips.bind(this)

    }
    handleChangeCheckbox(checked) {
        this.setState({ checked });
    }


    componentDidMount() {
        this.getListPronosticsTips()
        console.log(this.getListPronostics)
    }


    getListPronosticsTips = () => {

        getListPronosticsTodayTips().then(data => {
            this.setState(
                {
                    pronosticListTodayTips: [...data]
                },
                () => {
                    console.log(this.state.pronosticListTodayTips)
                }
            )
        })

        getListOldTipsYesterday().then(data => {
            this.setState(
                {
                    oldTipsYesterday: [...data]
                }
            )
        })

        getListOldTipsTwoDayAgo().then(data => {
            this.setState(
                {
                    oldTipsTwoDayAgo: [...data]
                }
            )
        })

        getListOldTipsThreeDayAgo().then(data => {
            this.setState(
                {
                    oldTipsThreeDayAgo: [...data]
                }
            )
        })

        getListPronosticsVIP().then(data => {
            this.setState(
                {
                    pronosticListVip: [...data]
                }
            )
        })

        getListPronosticsComboTips().then(data => {
            this.setState(
                {
                    pronosticListComboTips: [...data]
                }
            )
        })
    }

    onChangeDateOldTips = e => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    render() {
        let currentPronostics;
        if (this.props.typePronosticActive === "VIP Tips") {
            currentPronostics = this.state.pronosticListVip
        }
        else if (this.props.typePronosticActive === "Today's Tips") {
            currentPronostics = this.state.pronosticListTodayTips
        }
        else if (this.props.typePronosticActive === "Combo Tips") {
            currentPronostics = this.state.pronosticListComboTips
        }
        else {
            if (this.state.currentDateOldTips === '1 Day Ago')
                currentPronostics = this.state.oldTipsYesterday
            else if (this.state.currentDateOldTips === '2 Day Ago')
                currentPronostics = this.state.oldTipsTwoDayAgo
            else
                currentPronostics = this.state.oldTipsThreeDayAgo

        }

        return (
            <div>
                <Card>
                    <CardHeader>
                        <CardTitle tag="h4"><b>{this.props.typePronosticActive}  --  {(this.props.typePronosticActive === "Old Tips" ? ' Journe Precedente' : " Aujourd'hui")}</b></CardTitle>
                    </CardHeader>
                    <br /><br />
                    {(this.props.typePronosticActive === "Old Tips" ?
                        <Col className="pr-md-1" md="4">
                            <label>Choisir Journe</label>
                            <Input type="select" name="currentDateOldTips" onChange={this.onChangeDateOldTips.bind(this)}
                                placeholder="Choisir Journe">
                                <option value="1 Day Ago">Hier</option>
                                <option value="2 Day Ago">Avant-Hier</option>
                                <option value="3 Day Ago">Il y'a 3 Jours</option>
                            </Input>
                        </Col> :
                        <ModalAddPronostic pronosticTips={this.props.typePronosticActive} />
                    )}


                    <br /><br />
                    <CardBody>
                        <Table className="tablesorter" responsive>
                            <thead className="text-primary">
                                <tr>
                                    <th>Pays</th>
                                    <th>Championnat</th>
                                    <th>Rencontre</th>
                                    <th>Pronostic</th>
                                    <th>Heure</th>

                                    <th className="text-center">Action</th>
                                </tr>
                            </thead>
                            <tbody>

                                {currentPronostics.map((item, index) => (

                                    <tr key={index}>
                                        <td>{item.country}</td>
                                        <td>{item.championnat}</td>
                                        <td>{item.team_home} - {item.team_away}</td>
                                        <td>{item.match_event}</td>
                                        <td>{item.match_time}</td>
                                        <td className="text-center">
                                            {(item.type_pronostic === this.props.typePronosticActive ?
                                                <div>
                                                    <Tooltip
                                                        id="tooltip-top"
                                                        title="Consulter Pronostic"
                                                        placement="top">
                                                        <IconButton
                                                            aria-label="Edit" onClick={this.handleClickOpenModalAddPronostic}>
                                                            <VisibilityIcon
                                                                style={{ color: '2083F7' }} />
                                                        </IconButton>
                                                    </Tooltip>
                                                    <Tooltip
                                                        id="tooltip-top-start"
                                                        title="Modifier Pronostic"
                                                        placement="top">
                                                        <IconButton
                                                            aria-label="DeleteForeverIcon">
                                                            <Edit
                                                                style={{ color: '00DDCD' }} />
                                                        </IconButton>
                                                    </Tooltip>
                                                    <Tooltip
                                                        id="tooltip-top-start"
                                                        title="Supprimer Pronostic"
                                                        placement="top">
                                                        <IconButton
                                                            aria-label="DeleteForeverIcon">
                                                            <DeleteForeverIcon style={{ color: 'red' }} />
                                                        </IconButton>
                                                    </Tooltip>
                                                </div> :
                                                <div>
                                                    <Switch onChange={this.handleChangeCheckbox} checked={this.state.checked} />


                                                </div>)}

                                        </td>

                                    </tr>
                                ))}
                            </tbody>
                        </Table>
                    </CardBody>
                </Card>

            </div>
        );
    }
}

export default VipTipsTable;
