import axios from 'axios'

export const getCountry = () => {
    return axios
        .get('/api/country', {
            headers: { 'Content-Type': 'application/json' }
        })
        .then(res => {
            return res.data
        })
}
export const getChampionnatByCountry = (country) => {
    return axios
        .get(`/api/championnatbycountry/${country}`, {
            headers: { 'Content-Type': 'application/json' }
        })
        .then(res => {
            return res.data
        })
} 





