import React from "react";
import classnames from "classnames";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { loginUser, setUserLoading } from "../actions/authActions";
import loader from "../assets/img/loader.gif";

// reactstrap components
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  CardImg,
  CardTitle,
  Label,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Container,
  Row,
  Col
} from "reactstrap";


class LoginPage extends React.Component {

  constructor() {
    super();
    this.state = {
      email: "",
      password: "",
      errors: "",
      isLoading:false
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.auth.isAuthenticated) {
      this.props.history.push("/admin/dashboard"); // push user to dashboard when they login
     
    }
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors,

      });
    }
    if (nextProps.auth.loading) {
      this.setState({
        isLoading: nextProps.auth.loading,
      });
    }
  }

  onChange = e => {
    this.setState({ [e.target.id]: e.target.value });
  };
  onSubmit = e => {
    e.preventDefault();
    const userData = {
      email: this.state.email,
      password: this.state.password
    };

    this.props.loginUser(userData); // since we handle the redirect within our component, we don't need to pass in this.props.history as a parameter
  };

  state = {
    squares1to6: "",
    squares7and8: ""
  };

  componentDidMount() {
    this.props.setUserLoading(false);
    document.body.classList.toggle("register-page");
    document.documentElement.addEventListener("mousemove", this.followCursor);
  }
  componentWillUnmount() {
    document.body.classList.toggle("register-page");
    document.documentElement.removeEventListener(
      "mousemove",
      this.followCursor
    );
  }
  followCursor = event => {
    let posX = event.clientX - window.innerWidth / 2;
    let posY = event.clientY - window.innerWidth / 6;
    this.setState({
      squares1to6:
        "perspective(500px) rotateY(" +
        posX * 0.05 +
        "deg) rotateX(" +
        posY * -0.05 +
        "deg)",
      squares7and8:
        "perspective(500px) rotateY(" +
        posX * 0.02 +
        "deg) rotateX(" +
        posY * -0.02 +
        "deg)"
    });
  };


  render() {
    const { errors } = this.state;
    return (
      <>
        <div className="wrapper">
          <div className="page-header">
            <div className="page-header-image" />
            <div className="content">
              <Container>
                <Row>
                  <Col className="offset-lg-0 offset-md-3" lg="5" md="6">
                    <div
                      className="square square-7"
                      id="square7"
                      style={{ transform: this.state.squares7and8 }}
                    />
                    <div
                      className="square square-8"
                      id="square8"
                      style={{ transform: this.state.squares7and8 }}
                    />
                    <Form className="form" noValidate onSubmit={this.onSubmit}>
                      <Card className="card-register">
                        <CardHeader >
                          <CardImg
                            alt="..."
                            src={require("../assets/img/square-purple-1.png")}
                          />
                          <CardTitle tag="h5" >Login</CardTitle>
                        </CardHeader>
                        <CardBody>
                        
                        {(typeof(errors) == "string" ? <div className="text-danger">{errors}</div> :<div className="text-danger">{errors.errors}</div>)}      
                          <InputGroup
                            className={classnames({
                              "input-group-focus": this.state.fullNameFocus
                            })}
                          >

                          </InputGroup>
                          <InputGroup
                            className={classnames({
                              "input-group-focus": this.state.emailFocus
                            })}
                          >
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <i className="tim-icons icon-email-85" />
                              </InputGroupText>
                            </InputGroupAddon>
                            <Input
                              placeholder="Email"
                              type="text"
                              onChange={this.onChange}
                              value={this.state.email}
                              error={errors.email}
                              id="email"
                              type="email"
                              onFocus={e => this.setState({ emailFocus: true })}
                              onBlur={e => this.setState({ emailFocus: false })}
                            />
                          </InputGroup>
                          <InputGroup
                            className={classnames({
                              "input-group-focus": this.state.passwordFocus
                            })}
                          >
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <i className="tim-icons icon-lock-circle" />
                              </InputGroupText>
                            </InputGroupAddon>
                            <Input
                              placeholder="Mot de Passe"
                              type="text"
                              onChange={this.onChange}
                              value={this.state.password}
                              error={errors.password}
                              id="password"
                              type="password"
                              className={classnames("", {
                                invalid: errors.password || errors.passwordincorrect
                              })}
                              onFocus={e =>
                                this.setState({ passwordFocus: true })
                              }
                              onBlur={e =>
                                this.setState({ passwordFocus: false })
                              }
                            />

                          </InputGroup>
                          <FormGroup check className="text-left">
                            <Label check>
                              <Input type="checkbox" />
                              <span className="form-check-sign" />Se Rappelez de Moi{" "}

                            </Label>
                          </FormGroup>

                        </CardBody>
                        <CardFooter>
                          <Button className="btn-round" type="submit" color="primary" size="lg">
                            Se Connecter 
                             {(this.state.isLoading ? <img src={loader} width="20" height="20" alt='loader' /> : "")}
                          </Button>

                        </CardFooter>
                      </Card>
                    </Form>

                  </Col>
                </Row>
                <div className="register-bg" />
                <div
                  className="square square-1"
                  style={{ transform: this.state.squares1to6 }}
                />
                <div
                  className="square square-2"
                  style={{ transform: this.state.squares1to6 }}
                />
                <div
                  className="square square-3"
                  style={{ transform: this.state.squares1to6 }}
                />
                <div
                  className="square square-4"
                  style={{ transform: this.state.squares1to6 }}
                />
                <div
                  className="square square-5"
                  style={{ transform: this.state.squares1to6 }}
                />
                <div
                  className="square square-6"
                  style={{ transform: this.state.squares1to6 }}
                />
              </Container>
            </div>
          </div>
        </div>
      </>
    );
  }
}

LoginPage.propTypes = {
  loginUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
};
const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors,
});

export default connect(
  mapStateToProps,
  { loginUser, setUserLoading }
)(LoginPage);


