import React from "react";
import Select from 'react-select';
import { getCurrentDate, getTomorrowDate, get2DayAfterDate } from '../../variables/Date/Date'
import { getCountry, getChampionnatByCountry } from "../../Services/CountryWithChampionnat/CountryChampionnatService";
import { getListPronosticsVIP, getListPronosticsComboTips, getListPronosticsTodayTips, addPronostic, deletePronostic, updatePronostic } from '../../Services/Pronostics/PronosticsService';

// reactstrap components
import {
    Col, FormGroup, Input, Row, Button, ModalBody, ModalHeader, ModalFooter, Modal, Form,
} from "reactstrap";


const dateToday = getCurrentDate('-')
const customStyles = {
    option: (provided, state) => ({
        ...provided,
        color: state.isSelected ? 'black' : 'blue',
    }),
    singleValue: (provided, state) => {
        const opacity = state.isDisabled ? 0.5 : 0.8;
        const transition = 'opacity 300ms';

        return { ...provided, opacity, transition };
    }
}
class ModalAddPronostic extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            selectedOption: null,
            dataCountry: null,
            match_date: dateToday,
            dataChampionnatByCountry: null,
            isDisabledChampionnat: true,
            country: '',
            championnat: '',
            match_time: '',
            team_home: '',
            team_away: '',
            odd: '',
            match_event: '',
            editDisabled: false,
            deleteEnabled: false,

        };
        this.onChangeInputStates = this.onChangeInputStates.bind(this)
        this.onSubmit = this.onSubmit.bind(this)

    }




    onChangeInputStates = e => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    onSubmit = e => {
        e.preventDefault()
        addPronostic(this.props.pronosticTips, this.state.country, this.state.championnat, this.state.match_date,
            this.state.match_time, this.state.team_home, this.state.team_away, this.state.odd, this.state.match_event).then(() => {
            })

        return this.setState({
            setOpenModal: false,
            country: '',
            championnat: '',
            match_date: '',
            match_time: '',
            team_home: '',
            team_away: '',
            odd: '',
            match_event: '',
        });

    }

    handleClickOpenModalAddPronostic = () => {
        return this.setState({
            setOpenModal: true,
            country: '',
            championnat: '',
            match_time: '',
            team_home: '',
            team_away: '',
            odd: '',
            match_event: '',
        });
    };
    handleCloseModalAddPronostic = () => {
        return this.setState({ setOpenModal: false, editDisabled: false, deleteEnabled: false });
    };
    componentDidMount() {
        getCountry().then(data => {
            this.setState(
                {
                    dataCountry: data
                })
            console.log(this.state.dataCountry)
        })
    }

    getChampionnatByCountryList = country => {
        getChampionnatByCountry(country).then(data => {
            this.setState(
                {
                    dataChampionnatByCountry: data
                })
        })
    }

    handleSelectChangeCountry = selectedOption => {
        this.setState({
            selectedOption,
            isDisabledChampionnat: false,
            country: selectedOption.value
        });
        this.getChampionnatByCountryList(selectedOption.value)
        console.log(`Option selected:`, selectedOption.value);

    };

    handleSelectChangeChampionnat = selectedOption => {
        this.setState({
            championnat: selectedOption.value
        });
        console.log(`Option selected:`, selectedOption.value);
    };


    render() {
        const { selectedOption } = this.state
        console.log(this.props.pronosticTips)

        return (
            <Col>
                <Button onClick={this.handleClickOpenModalAddPronostic} color="primary">
                    Ajouter
        </Button>
                <Modal modalClassName="modal-black" isOpen={this.state.setOpenModal} toggle={this.handleCloseModalAddPronostic} size="lg">
                    <ModalHeader toggle={this.handleCloseModalAddPronostic}>
                        <span>{this.state.deleteEnabled ? `Suppression Pronostic ${this.props.pronosticTips}` : (this.state.editDisabled ? `Modification Pronostic ${this.props.pronosticTips}` : `Ajouter Pronostic ${this.props.pronosticTips}`)}</span>

                    </ModalHeader>
                    <ModalBody>
                        <Form onSubmit={this.onSubmit}>
                            {(this.props.deleteEnabled
                                ? <div className="text-center">Voulez-Vous Vraiment Supprimer ?</div>
                                :
                                <Row>
                                    <Row>
                                        <Col className="pr-md-1" md="8">
                                            <label>Pays</label>
                                            <Select
                                                value={selectedOption}
                                                onChange={this.handleSelectChangeCountry}
                                                options={this.state.dataCountry}
                                                name="country"
                                                isSearchable={true}
                                                isClearable={true}
                                                placeholder="Choisir Pays"
                                                styles={customStyles}
                                            />

                                        </Col>
                                        <Col className="pr-md-1" md="8">

                                            <label>Championnat</label>
                                            <Select
                                                isDisabled={this.state.isDisabledChampionnat}
                                                options={this.state.dataChampionnatByCountry}
                                                onChange={this.handleSelectChangeChampionnat}
                                                isSearchable={true}
                                                name="championnat"
                                                isClearable={this.state.isClearableChampionnat}
                                                styles={customStyles}
                                                placeholder="Choisir Championnat"
                                            />
                                        </Col>
                                        <Col className="pr-md-1" md="8">

                                            <label>Date Match</label>
                                            <Input type="select" name="match_date"
                                                placeholder="Choisir Date" onChange={this.onChangeInputStates.bind(this)}>
                                                <option value={getCurrentDate('-')}>Aujourd'hui</option>
                                                <option value={getTomorrowDate('-')}>Demain</option>
                                                <option value={get2DayAfterDate('-')}>Apres Demain</option>

                                            </Input>

                                        </Col>

                                    </Row>
                                    <br />
                                    <Row>
                                        <Col className="pr-md-1" md="4">

                                            <label>Equipe Domicile</label>
                                            <Input
                                                onChange={this.onChangeInputStates.bind(this)}
                                                placeholder="Entrer Equipe a Domcile"
                                                type="text"
                                                name="team_home"
                                            />

                                        </Col>
                                        <Col className="pr-md-1" md="4">

                                            <label>Equipe Exterieur</label>
                                            <Input
                                                onChange={this.onChangeInputStates.bind(this)}
                                                placeholder="Entrer Equipe a L'exterieur"
                                                type="text"
                                                name="team_away"
                                            />

                                        </Col>
                                        <Col className="pr-md-1" md="4">

                                            <label>Debut Match</label>
                                            <Input
                                                onChange={this.onChangeInputStates.bind(this)}
                                                placeholder="Entrer Heure Du Match"
                                                type="time"
                                                name="match_time"
                                            />

                                        </Col>

                                    </Row>
                                    <br />
                                    <Row>
                                        <Col className="pr-md-1" md="4">

                                            <label>Pronostic</label>
                                            <Input
                                                onChange={this.onChangeInputStates.bind(this)}
                                                placeholder="Entrer Pronostic"
                                                type="text"
                                                name="match_event"
                                            />

                                        </Col>
                                        <Col className="pr-md-1" md="4">
                                            <FormGroup>
                                                <label>Cote</label>
                                                <Input
                                                    onChange={this.onChangeInputStates.bind(this)}
                                                    placeholder="Entrer Cote"
                                                    type="text"
                                                    name="odd"
                                                />
                                            </FormGroup>
                                        </Col>

                                    </Row>
                                </Row>
                            )
                            }
                        </Form>
                    </ModalBody>
                    <ModalFooter>
                        <Button color="warning" onClick={this.handleCloseModalAddPronostic}>Fermer</Button>
                        {this.props.deleteEnabled
                            ? <Button color="primary" type="submit">Supprimer</Button>
                            : (this.props.editDisabled
                                ? <Button color="primary" type="submit" >Modifier</Button>

                                : <Button color="primary" type="submit" onClick={this.onSubmit.bind(this)}>Ajouter</Button>

                            )
                        }
                    </ModalFooter>
                </Modal>

            </Col >

        );
    }
}

export default ModalAddPronostic;
