class App extends Component {

    render() {
      return (
        <Provider store={store}>
        <Router history={hist}>
          <Switch>
          <Route path="/login" render={props => <LoginPage {...props} />} />
            <Route path="/admin" render={props => <AdminLayout {...props} />} />
            <Redirect from="/" to={(store.getState().auth.isAuthenticated ? "/admin/dashboard" : "/login")} />
         </Switch>
        </Router>
      </Provider>
      );
    }
  }

  export default App;


