import React from "react";
//import components materialse 
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import Edit from "@material-ui/icons/Edit";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import { getList, addItem, deleteItem, updateItem } from '../Services/ParamsNotificationService'
import loader from "../assets/img/loader.gif";

// reactstrap components
import {
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  Table,
  Row,
  Col,
  Button,
  ModalBody,
  ModalHeader,
  ModalFooter,
  Modal,
  Form,
  Input,
  ButtonGroup,
  Container
} from "reactstrap";

class Admins extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: '',
      title: '',
      description: '',
      editDisabled: false,
      deleteEnabled: false,
      items: [],
      setOpenModal: false,
      isLoading: true

    };

    this.onSubmit = this.onSubmit.bind(this)
    this.onChange = this.onChange.bind(this)
  }
  componentDidMount() {
    this.getAll()
  }

  onChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  getAll = () => {
    getList().then(data => {
      this.setState(
        {
          title: '',
          description: '',
          isLoading: false,
          items: [...data]
        },
        () => {
          console.log(this.state.items)
        }
      )
    })
  }


  onSubmit = e => {
    e.preventDefault()
    addItem(this.state.title, this.state.description).then(() => {
      this.getAll()
    })
    this.setState({
      title: '',
      description: '',
      setOpenModal: false
    })
  }

  onUpdate = e => {
    e.preventDefault()
    updateItem(this.state.title, this.state.description, this.state.id).then(() => {
      this.getAll()
    })
    this.setState({
      editDisabled: false,
      setOpenModal: false,
      deleteEnabled: false
    })
  }

  onEdit = (itemid, e) => {
    e.preventDefault()
    this.setState({ setOpenModal: true });
    var data = [...this.state.items]
    data.forEach((item) => {
      if (item.id === itemid) {
        this.setState({
          id: item.id,
          title: item.title,
          description: item.description,
          editDisabled: true,
          deleteEnabled: false
        })
      }
    })
  }

  onSubmitDelete = e => {
    e.preventDefault()
    deleteItem(this.state.id)
    this.getAll()
    this.setState({ deleteEnabled: false, setOpenModal: false })

  }

  onDelete = (val, e) => {
    e.preventDefault()
    var data = [...this.state.items]
    data.filter(function (item, index) {
      if (item.id === val) {
        data.splice(index, 1)
      }
      return true
    })
    this.setState({ id: val, setOpenModal: true, deleteEnabled: true, editDisabled: false })
  }

  handleClickOpen = () => {
    return this.setState({
      setOpenModal: true,
      editDisabled: false,
      deleteEnabled: false,
      title: "",
      description: ""
    });
  };

  handleClose = () => {
    return this.setState({ setOpenModal: false });
  };
  render() {
    let data;
    if (this.state.isLoading) {
      data = <p className="text-center"><img src={loader} width="79" height="79" alt='loader' /></p>
    } else {
      data =
        <Col className="ml-auto mr-auto" md="12" xl="12">
          <div className="mb-3">
            <small className="text-uppercase font-weight-bold">
              Details
                </small>
          </div>
          <Card>
            <CardHeader>
              <CardTitle tag="h4">Liste Des Notifications Predefini</CardTitle>
            </CardHeader>
            <br />
            <ButtonGroup size="xm">
              <Button onClick={this.handleClickOpen} color="primary" >
                Ajouter
          </Button>
            </ButtonGroup>
            <br />
            <CardBody>
              <Table className="tablesorter" >
                <thead className="text-primary">
                  <tr>
                    <th>Titre </th>
                    <th>Description</th>
                    <th className="text-center">Action</th>
                  </tr>
                </thead>
                <tbody >
                  {this.state.items.map((item, index) => (
                    <tr key={index}>
                      <td>{item.title}</td>
                      <td>{item.description}</td>
                      <td className="text-center">

                        <Tooltip
                          id="tooltip-top-start"
                          title="Modifier Pronostic"
                          placement="top">
                          <IconButton
                            aria-label="DeleteForeverIcon" onClick={this.onEdit.bind(
                              this,
                              item.id
                            )}>
                            <Edit
                              style={{ color: '00DDCD' }} />
                          </IconButton>
                        </Tooltip>
                        <Tooltip
                          id="tooltip-top-start"
                          title="Supprimer Pronostic"
                          placement="top">
                          <IconButton
                            aria-label="DeleteForeverIcon" onClick={this.onDelete.bind(this,
                              item.id)}>
                            <DeleteForeverIcon style={{ color: 'red' }} />
                          </IconButton>
                        </Tooltip>

                      </td>
                    </tr>
                  ))}
                </tbody>
              </Table>

            </CardBody>
          </Card>
        </Col>
    }
    
    return (
      <div className="content">
        <Container>
          <div className="title">
            <h3 className="mb-3">Parametres</h3>
          </div>
          <Row>

            {data}


          </Row>
          <Modal modalClassName="modal-black" isOpen={this.state.setOpenModal} toggle={this.handleClose} size="xs">
            <ModalHeader toggle={this.handleClose}>
              <span>{this.state.deleteEnabled ? 'Supprimer La Notification' : (this.state.editDisabled ? 'Modifier la Notification Predefini' : 'Ajouter Une Notification Predefini')}</span>
            </ModalHeader>
            <ModalBody>
              <Form onSubmit={this.onSubmit}>
                <Row>
                  {(this.state.deleteEnabled
                    ? <div className="text-center">Voulez-Vous Vraiment Supprimer ?</div>
                    :
                    <div>
                      <Row><Col className="pr-md-1" md="12">
                        <label>Titre</label>
                        <Input
                          className="form-control"
                          id="title"
                          name="title"
                          value={this.state.title || ''}
                          onChange={this.onChange.bind(this)}
                          placeholder="Entrer Titre"
                          type="text"
                        />
                      </Col>
                      </Row>
                      <Row>
                        <Col className="pr-md-1" md="12">
                          <label>Description</label>
                          <Input
                            className="form-control"
                            id="description"
                            name="description"
                            value={this.state.description || ''}
                            onChange={this.onChange.bind(this)}
                            placeholder="Entrer Description"
                            type="textarea"
                          />
                        </Col>
                      </Row>
                    </div>
                  )
                  }


                </Row>
              </Form>
            </ModalBody>
            <ModalFooter>
              <Button color="warning" onClick={this.handleClose}>Fermer</Button>
              {this.state.deleteEnabled
                ? <Button color="primary" type="submit" onClick={this.onSubmitDelete.bind(this)}>Supprimer</Button>
                : (this.state.editDisabled
                  ? <Button color="primary" type="submit" onClick={this.onUpdate.bind(this)}>Modifier</Button>

                  : <Button color="primary" type="submit" onClick={this.onSubmit.bind(this)}>Ajouter</Button>

                )
              }
            </ModalFooter>
          </Modal>
        </Container>
      </div>
    );
  }
}

export default Admins;
