import axios from 'axios'

export const getList = () => {
    return axios
        .get('/api/parametrenotifications', {
            headers: { 'Content-Type': 'application/json'}
        })
        .then(res => {
            return res.data
        })
}

export const addItem = (title,description) => {
    return axios
        .post(
            '/api/parametrenotifications',
            {
                title: title,
                description:description
            },
            {
                headers: { 'Content-Type': 'application/json' }
            }
        )
        .then(function(response) {
            console.log(response)
        })
}

export const deleteItem = id => {
    axios
        .delete(`/api/parametrenotifications/${id}`, {
            headers: { 'Content-Type': 'application/json' }
        })
        .then(function(response) {
            console.log(response)
        })
        .catch(function(error) {
            console.log(error)
        })
}

export const updateItem = (title,description, id) => {
    return axios
        .put(
            `/api/parametrenotifications/${id}`,
            {
                title: title,
                description:description
            },
            {
                headers: { 'Content-Type': 'application/json' }
            }
        )
        .then(function(response) {
            console.log(response)
        })
}
