export const addPronostic = (type_pronostic,country,championnat,match_date,match_time,team_home,team_away,odd,match_event) => {
    return axios
        .post(
            '/api/pronostics',
            {
                type_pronostic: type_pronostic,
                country:country,
                championnat:championnat,
                match_date:match_date,
                match_time:match_time,
                team_home:team_home,
                team_away:team_away,
                odd:odd,
                match_event:match_event
            },
            {
                headers: { 'Content-Type': 'application/json' }
            }
        )
        .then(function(response) {
            console.log(response)
        })
}

export const deletePronostic = id => {
    axios
        .delete(`/api/pronostics/${id}`, {
            headers: { 'Content-Type': 'application/json' }
        })
        .then(function(response) {
            console.log(response)
        })
        .catch(function(error) {
            console.log(error)
        })
}

export const updatePronostic = (type_pronostic,country,championnat,match_date,match_time,team_home,team_away,odd,match_event, id) => {
    return axios
        .put(
            `/api/pronostics/${id}`,
            {
                type_pronostic: type_pronostic,
                country:country,
                championnat:championnat,
                match_date:match_date,
                match_time:match_time,
                team_home:team_home,
                team_away:team_away,
                odd:odd,
                match_event:match_event
            },
            {
                headers: { 'Content-Type': 'application/json' }
            }
        )
        .then(function(response) {
            console.log(response)
        })
}
