import React from "react";
import classnames from "classnames";
import VipTipsTable from "../Tables/VipTipsTable"
// reactstrap components
import {TabContent,TabPane,Container,Row,Col,Card,CardHeader,CardBody,Nav,NavItem,NavLink,Badge} from "reactstrap";
class PronosticsTabs extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      typePronosticActive: "VIP Tips",
      textTabs: 4,
      pronosticTips: ["VIP Tips", "Today's Tips", "Combo Tips","Old Tips"]
    };
  }
  
  toggleTabs = (e, index) => {
    e.preventDefault()
    this.setState({
      typePronosticActive: index
    });
  };

  render() {
    return (
      <div className="section section-tabs">
        <Container>
          <div className="title">
            <h3 className="mb-3">Football</h3>
          </div>
          <Row>
            <Col className="ml-auto mr-auto" md="12" xl="12">
              <div className="mb-3">
                <small className="text-uppercase font-weight-bold">
                  Pronostics
                </small>
              </div>
              <Card>
                <CardHeader>
                  <Nav className="nav-tabs-primary" role="tablist" tabs>
                    {this.state.pronosticTips.map((item) => (
                      <NavItem>
                        <NavLink
                          className={classnames({
                            active: this.state.typePronosticActive === item
                          })}
                          onClick={(e) => this.toggleTabs(e, item)}
                          href="#pablo"
                        >
                          <i className="tim-icons icon-notes" /> {item} <Badge color="primary"> 01</Badge>
                        </NavLink>
                      </NavItem>
                    ))}
                  </Nav>
                </CardHeader>
                <CardBody>
                  <TabContent
                    className="tab-space"
                    activeTab={"link" + this.state.typePronosticActive}
                  >
                    {this.state.pronosticTips.map((item) => (
                      <TabPane tabId={'link' + item} >
                        <p>
                          <VipTipsTable typePronosticActive={this.state.typePronosticActive} />
                        </p>
                      </TabPane>
                    ))}
                  </TabContent>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default PronosticsTabs;